import { get, post } from '../../libs/request.js';
export default {
	data() {
		return {
			checked: []
		};
	},
	methods: {
		changed(e) {
			this.checked = e.detail.value;
		},
		submit(e) {
			let sn = '88c12dc1c96f4ed1846454691ad74e77';
			// if(this.checked.length > 0){

			this.getWechart(sn);




