								},
								success() {
									console.info('step3')
									
								},
								fail() {
									//dosomething
								},
								complete() {
									//dosomething
									console.info('complete')
									uni.navigateTo({
										url: `loaning?orderNum=${res.data.orderNum}`,
									});
								}
							});
						},
						fail() {
							//dosomething
							console.info('失败');
						},
						complete() {
							//dosomething
							console.info('complete1')
							uni.navigateTo({
